import "./App.css"
import { useState } from "react"
import { calculateGameScore } from "bowlingCalculator/bowling.util"

function App() {
  const [rawInput, setRawInput] = useState("")
  const [result, setResult] = useState("")
  const [jsonInput, setJsonInput] = useState([])

  const setExampleJson = () => {
    setRawInput(
      JSON.stringify([
        [1, 2],
        [10, "nil"],
        [5, 4],
        [7, 3],
        [10, "nil"],
        [10, "nil"],
        [1, 4],
        [6, 2],
        [7, 3],
        [10, 3, 7],
      ]),
    )

    // handleCalculateGame()
  }

  const handleCalculateGame = () => {
    try {
      const jsonInput = JSON.parse(rawInput)

      setJsonInput(jsonInput)
      setResult(calculateGameScore(jsonInput))
    } catch {
      setResult("Invalid Input (not valid json)")
    }
  }

  return (
    <div className="App">
      <header className="App-header">
        <h1>Simple Bowling Calculator</h1>

        <button
          className="button"
          style={{ marginBottom: "15px", backgroundColor: "#007BFF" }}
          onClick={setExampleJson}
        >
          Use Example Game JSON
        </button>

        <button className="button" style={{ marginBottom: "15px" }} onClick={handleCalculateGame}>
          Calculate Game
        </button>

        <textarea
          className="textarea"
          placeholder="Raw Game Input"
          value={rawInput}
          onChange={(e) => setRawInput(e.target.value)}
        ></textarea>

        {result && typeof result == "string" && result}
        {result && typeof result != "string" && result.errors && result.errors.length > 0 && (
          <div>
            {result.errors.map((error, index) => (
              <div key={error + index}>{error}</div>
            ))}
          </div>
        )}
        {result && typeof result != "string" && (!result.errors || result.errors.length === 0) && (
          <div className="result">
            <p>
              <div style={{ display: "flex", flexWrap: "wrap" }}>
                {result.frameScores.map((frameScore, index) => (
                  <div className="bowling-frame" key={index}>
                    <div className="rolls">
                      <div className="roll first-roll">{jsonInput[index][0]}</div>
                      <div className="roll second-roll">{jsonInput[index][1]}</div>
                    </div>
                    <div className="total-score">{frameScore}</div>
                  </div>
                ))}
              </div>
            </p>
            <p>Game Score: {result.gameScore}</p>
          </div>
        )}
      </header>
    </div>
  )
}

export default App
